/** @jsxImportSource @emotion/react */
import React, { useEffect, useMemo } from 'react'
import { RecoilValue, useRecoilValue } from 'recoil'
import MuiSkeleton from '@mui/material/Skeleton'
import IconButton from '@mui/material/IconButton'
import OpenInNewIcon from '@mui/icons-material/OpenInNew'
// import log from 'loglevel'

import { Site } from '../../types'
import { withSuspense } from '../withSuspense'
import { calculateFit } from './calculateFit'
import { SitesMap } from './SitesMap'
import { useSitesMapController, IDEAL_CLOSE_ZOOM, DEFAULT_VIEWPORT } from './useSitesMapController'
import { useNavigate } from '../AppRouter'
import { useSetSitesListingSource, SitesListingSource } from '../SitesListing'

interface SitesMapPreviewProps {
  sitesSelector: RecoilValue<Site[]>
  sitesListingSource: SitesListingSource
}

const height = 300
const width = 300

export const SitesMapPreview = withSuspense(
  ({ sitesSelector, sitesListingSource }: SitesMapPreviewProps): JSX.Element => {
    const sites = useRecoilValue(sitesSelector)
    const navigate = useNavigate()
    const setSitesListingSource = useSetSitesListingSource()

    const initialViewport = useMemo(() => {
      if (sites.length === 0)
        return {
          ...DEFAULT_VIEWPORT,
          zoom: 0.5,
        }
      return calculateFit(sites, { height, width, maxZoom: IDEAL_CLOSE_ZOOM })
    }, [])

    const onSitesClick = (targetSites: Site[]) => {
      const { viewport } = controller
      if (targetSites.length === 1) {
        setSitesListingSource(sitesListingSource)
        window.setTimeout(() => navigate.siteDetail({ siteId: targetSites[0].id }))
      } else {
        if (!viewport.width || !viewport.height) throw new Error('No viewport.')
        controller.fitSites(targetSites)
      }
    }

    const handleExpandMapClick = () => {
      setSitesListingSource(sitesListingSource)
      window.setTimeout(() => {
        if (sites.length === 1) {
          navigate.siteDetail({ siteId: sites[0].id })
        } else {
          navigate.sites()
        }
      })
    }

    const controller = useSitesMapController({ sites, initialViewport, onSitesClick })

    useEffect(() => {
      // refocus when sites changes; has no effect when map first loaded with initialViewport (desired behaviour)
      if (sites.length === 0) return
      controller.smartFitAll()
    }, [sites])

    return (
      <div css={{ height, width, position: 'relative' }}>
        <IconButton
          title="Expand Sites Map"
          onClick={handleExpandMapClick}
          css={{ position: 'absolute', left: 0, right: 10, zIndex: 100 }}
        >
          <OpenInNewIcon />
        </IconButton>
        <SitesMap controller={controller} minimal></SitesMap>
      </div>
    )
  },
  () => (
    <div css={{ height, width }}>
      <MuiSkeleton width={width} height={height} variant="rectangular" />
    </div>
  )
)
