import { BoxDetailProps } from './BoxDetail'
import { Box } from '../../types'
import { useApi } from '../../data/api'
import { useNotifications } from '../AppNotifications'
import { useUpdatableState } from '../../lib/useUpdatableState'
import { updateBox } from '../../data/calls'
import { useInvalidate } from '../../data/selectors/invalidate'

interface Fields {
  boxStateId: number
  measurements: boolean
  midges: boolean
  description: string
  customerId: number
  alias: string
  modelIds: number[]
}

type FieldErrors = Partial<Record<keyof Fields, boolean>> | undefined

export type ControllerState = {
  // box?: Box
  values: Fields
  errors: FieldErrors
  editMode: 'stopped' | 'started' | 'stopping'
  isDirty: boolean
}

type InternalControllerState = Omit<ControllerState, 'errors'>

export interface ControllerActions {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  setFieldValue: (fieldName: keyof Fields, value: any) => void
  startEdit: () => void
  stopEdit: (cancel?: boolean) => void
}

// const getFieldErrors = (values: Fields): FieldErrors => {
//   const errors = {}
//   if (!Object.values(errors).some((value) => value)) return undefined
//   return errors
// }

const getInitialState = (box: Box): InternalControllerState => ({
  values: {
    boxStateId: box.boxStateId ?? -1,
    measurements: !!box.measurements,
    midges: !!box.midges,
    description: box.description ?? '',
    customerId: box.customerId,
    alias: box.alias,
    modelIds: box.modelIds ?? [],
  },
  editMode: 'stopped',
  isDirty: false,
})

export const useComponentController = ({ box }: Pick<BoxDetailProps, 'box'>): [ControllerState, ControllerActions] => {
  const api = useApi()
  const invalidate = useInvalidate()
  const notifications = useNotifications()

  const [state, setState, updateState] = useUpdatableState<InternalControllerState>(getInitialState(box))

  const actions = {
    // TODO: handle reset through useUpdatableState enhancement
    setFieldValue: async (fieldName: keyof Fields, value: unknown) => {
      updateState((draft) => {
        Object.assign(draft.values, { [fieldName]: value })
        draft.isDirty = true
      })
    },
    startEdit: () => {
      updateState((draft) => {
        draft.editMode = 'started'
      })
    },
    stopEdit: async (cancel?: boolean) => {
      if (cancel) {
        setState(getInitialState(box))
        return
      }
      updateState((draft) => {
        draft.editMode = 'stopping'
      })
      try {
        await updateBox(api, invalidate)({ boxId: box.id, ...state.values })
        updateState((draft) => {
          draft.editMode = 'stopped'
          draft.isDirty = false
        })
      } catch (err) {
        notifications.error(`Failed to save box: ${err}`)
        updateState((draft) => {
          draft.editMode = 'started'
        })
      }
    },
  }

  // const errors = state.editMode === 'stopped' ? undefined : getFieldErrors(state.values)

  return [{ ...state, errors: undefined }, actions]
}
