import gql from 'graphql-tag'
import { Site as GraphSite } from '@namcbugdb/common'

import { Api } from '../api'
import { Site } from '../../types'
import { transformSite } from './fetchSites'

export const fetchSitesByProjectIdQuery = gql`
  query sites($projectId: Int) {
    sites(projectIds: [$projectId]) {
      records {
        siteId
        siteName
        longitude
        latitude
        usStateId
      }
      nextOffset
    }
  }
`

export const fetchSitesByProjectId =
  (api: Api) =>
  async (projectId: number): Promise<Site[]> => {
    const data = await api.query<{ sites: { records: GraphSite[] } }>(fetchSitesByProjectIdQuery, { projectId })
    const sites = data.sites.records

    return sites.map(transformSite)
  }
