/** @jsxImportSource @emotion/react */
import React from 'react'
import { useRecoilValue } from 'recoil'
import IconButton from '@mui/material/IconButton'
import CloseIcon from '@mui/icons-material/Close'
import MuiSkeleton from '@mui/material/Skeleton'

import { ReadOnlyField } from '../ReadOnlyField'
import { Site } from '../../types'
import { selectUsStates } from '../../data/selectors'
import { withSuspense } from '../withSuspense'
import { formatUtcDateTimeToAppDate } from '../../lib/formats'

export interface SiteDetailPanelProps {
  site?: Site
  onClose: () => void
  transitionMs: number
}

export const SiteDetailPanel = withSuspense(
  ({ site, onClose, transitionMs }: SiteDetailPanelProps): JSX.Element => {
    const usStates = useRecoilValue(selectUsStates)
    const isOpen = !!site

    return (
      <div
        css={{
          width: isOpen ? '400px' : 0,
          maxWidth: '50%',
          transition: `width ${transitionMs}ms`,
          position: 'relative',
          overflow: 'hidden',
        }}
      >
        {site && (
          <div
            css={{
              position: 'absolute',
              border: '1px solid lightgrey',
              width: '100%',
              height: '100%',
              boxSizing: 'border-box',
              padding: '0.5em',
            }}
          >
            <div css={{ display: 'flex', alignItems: 'center', paddingLeft: '0.8em' }}>
              <div css={{ fontWeight: 'bold', fontSize: '120%' }}>{site.name}</div>
              <div css={{ marginLeft: 'auto' }}>
                <IconButton onClick={onClose}>
                  <CloseIcon />
                </IconButton>
              </div>
            </div>

            <div
              css={{
                marginTop: '1em',
                display: 'grid',
                gridTemplateColumns: '1fr 1fr',
                gridTemplateRows: `
                  auto
                  auto
                  1em
                  auto
                  auto
                  1em
                  auto
                  1em
                  auto
                `,
                gap: '0.5em',
                gridTemplateAreas: `
                  "usState id"
                  "latitude longitude"
                  ". ."
                  "system ecosystem"
                  "waterbodyName waterbodyType"
                  ". ."
                  "sampleCount ."
                  ". ."
                  "createdDate updatedDate"
                `,
              }}
            >
              <ReadOnlyField
                gridArea="usState"
                label="State"
                value={usStates.find(({ id }) => id === site.usStateId)?.abbreviation}
              />
              <ReadOnlyField gridArea="id" label="ID" value={site.id} />
              <ReadOnlyField gridArea="latitude" label="Latitude" value={site.latitude} />
              <ReadOnlyField gridArea="longitude" label="Longitude" value={site.longitude} />
              <ReadOnlyField
                gridArea="sampleCount"
                label="# Samples"
                value={site.sampleCount ?? <MuiSkeleton sx={{ width: '3em' }} />}
              />
              <ReadOnlyField
                gridArea="system"
                label="System"
                value={site.system ?? <MuiSkeleton sx={{ width: '10em' }} />}
              />
              <ReadOnlyField
                gridArea="ecosystem"
                label="Ecosystem"
                value={site.ecosystem ?? <MuiSkeleton sx={{ width: '10em' }} />}
              />
              <ReadOnlyField
                gridArea="waterbodyType"
                label="Waterbody Type"
                value={site.waterbodyType ?? <MuiSkeleton sx={{ width: '10em' }} />}
              />
              <ReadOnlyField
                gridArea="waterbodyName"
                label="Waterbody"
                value={site.waterbodyName ?? <MuiSkeleton sx={{ width: '10em' }} />}
              />
              <ReadOnlyField
                gridArea="createdDate"
                label="Created"
                value={
                  site.createdDate ? (
                    formatUtcDateTimeToAppDate(site.createdDate)
                  ) : (
                    <MuiSkeleton sx={{ width: '7em' }} />
                  )
                }
              />
              <ReadOnlyField
                gridArea="updatedDate"
                label="Updated"
                value={
                  site.updatedDate ? (
                    formatUtcDateTimeToAppDate(site.updatedDate)
                  ) : (
                    <MuiSkeleton sx={{ width: '7em' }} />
                  )
                }
              />
            </div>
          </div>
        )}
      </div>
    )
  },
  () => null
)
