import gql from 'graphql-tag'
import { Site as GraphSite } from '@namcbugdb/common'

import { Api } from '../api'
import { Site } from '../../types'
import { transformSite } from './fetchSites'

export const fetchSitesByBoxIdQuery = gql`
  query sites($boxId: Int) {
    sites(boxIds: [$boxId]) {
      records {
        siteId
        siteName
        longitude
        latitude
        usStateId
      }
      nextOffset
    }
  }
`

export const fetchSitesByBoxId =
  (api: Api) =>
  async (boxId: number): Promise<Site[]> => {
    const data = await api.query<{ sites: { records: GraphSite[] } }>(fetchSitesByBoxIdQuery, { boxId })
    const sites = data.sites.records

    return sites.map(transformSite)
  }
