import gql from 'graphql-tag'

import { Api, ApiError } from '../api'
import { InvalidateFn } from '../selectors'

interface SampleSubmission {
  submitterId: number
  customerId: number | null
  data: {
    templateVersion: number
    appVersion: string
    samples: Record<string, string | number | boolean | null | undefined>[]
  }
}

export const createSampleSubmissionMutation = gql`
  mutation ($submitterId: Int, $customerId: Int, $data: JSONObject) {
    createSampleSubmission(submitterId: $submitterId, customerId: $customerId, data: $data)
  }
`

export const createSampleSubmission =
  (api: Api, invalidate: InvalidateFn) =>
  async (submission: SampleSubmission): Promise<number> => {
    const data = await api.mutation<{
      createSampleSubmission: number | null // NOTE: new box ID
    }>(createSampleSubmissionMutation, { ...submission, data: JSON.stringify(submission.data) })

    const id = data.createSampleSubmission
    if (!id) throw new ApiError('submission did not create')

    invalidate('boxes')
    return id
  }
