/** @jsxImportSource @emotion/react */
import React, { useMemo, useState } from 'react'
import { FlyToInterpolator, MapEvent, ViewportProps } from 'react-map-gl'
import { FeatureCollection } from 'geojson'
import log from 'loglevel'

import { Site } from '../../types'
import { calculateFit } from './calculateFit'

export interface SitesMapController {
  viewport: ViewportProps
  setViewport: React.Dispatch<React.SetStateAction<ViewportProps>>
  fitSites: (targetSites: Site[], options?: { padding?: number }) => void
  centerSite: (targetSite: Site, options?: { zoom?: number }) => void
  sitesData: FeatureCollection
  handleClick: (event: MapEvent) => void
  smartFitAll: () => void
  handleHover: (event: MapEvent) => void
  hoverInfo: HoverInfo | null
  isViewportReady: boolean
}

interface HoverInfo {
  node: React.ReactNode
  x: number
  y: number
}

interface UseSitesMapControllerParams {
  initialViewport?: ViewportProps
  onSitesClick?: (targetSites: Site[]) => void
  sites: Site[]
}

export const DEFAULT_VIEWPORT = {
  longitude: -97.6701409,
  latitude: 38.7250438,
  zoom: 3, // crude approximation as what you see depends on window size; this fits Lower 48 @ 600px
}

export const MAX_ZOOM = 22 // closest we ever want to be
export const IDEAL_CLOSE_ZOOM = 9 // good zoom for single point, and closest we ever want to be on map loading

export const SITES_LAYER_ID = 'namc-sites'

export const useSitesMapController = ({
  initialViewport,
  onSitesClick,
  sites,
}: UseSitesMapControllerParams): SitesMapController => {
  const [viewport, setViewport] = useState<ViewportProps>({ ...DEFAULT_VIEWPORT, ...initialViewport })
  const [hoverInfo, setHoverInfo] = useState<HoverInfo | null>(null)

  const sitesData = useMemo<FeatureCollection>(
    () => ({
      type: 'FeatureCollection',
      features: sites.map((site) => ({
        type: 'Feature',
        geometry: { type: 'Point', coordinates: [site.longitude, site.latitude] },
        properties: site,
      })),
    }),
    [sites]
  )

  const fitSites = (targetSites: Site[], options: { padding?: number } = {}) => {
    const { width, height } = viewport
    if (!width || !height) {
      log.debug('fitSites skipped: no viewport yet')
      return
    }

    const calculatedViewport = calculateFit(targetSites, { height, width, ...options })

    const transition = { transitionInterpolator: new FlyToInterpolator(), transitionDuration: 1000 }

    setViewport((vp) => ({
      ...vp,
      ...transition,
      ...calculatedViewport,
    }))
  }

  const centerSite = (site: Site, options: { zoom?: number } = {}) => {
    const { latitude, longitude } = site
    const { zoom = IDEAL_CLOSE_ZOOM } = options
    setViewport({
      ...viewport,
      longitude,
      latitude,
      zoom,
      transitionDuration: 750,
    })
  }

  const smartFitAll = () => {
    if (sites.length === 0) return

    if (sites.length === 1) {
      centerSite(sites[0])
      return
    }

    fitSites(sites)
  }

  const handleClick = (event: MapEvent) => {
    setHoverInfo(null)
    const targetSites =
      event.features &&
      event.features.filter((feature) => feature.layer?.source === SITES_LAYER_ID).map((feature) => feature.properties)
    if (targetSites?.length && onSitesClick) onSitesClick(targetSites)
  }

  const handleHover = (event: MapEvent) => {
    const mouseEvent = event.srcEvent as MouseEvent
    const { features = [] } = event
    const { offsetX: x, offsetY: y } = mouseEvent

    const targetSites = features.map((feature) => feature.properties) as Site[]

    if (targetSites.length === 0) {
      setHoverInfo(null)
      return
    }

    const node =
      targetSites.length < 6 ? (
        <div>
          {targetSites.map(({ id, name }) => (
            <div key={id} css={{ display: 'flex', justifyContent: 'center', marginTop: '0.1em' }}>
              <span css={{ whiteSpace: 'nowrap', background: '#ffffffaa', padding: '0.1em' }}>{name}</span>
            </div>
          ))}
        </div>
      ) : (
        <div>{targetSites.length} sites</div>
      )

    setHoverInfo({ node, x, y })
  }

  const isViewportReady = !!(viewport.width && viewport.height)

  return {
    viewport,
    setViewport,
    fitSites,
    centerSite,
    sitesData,
    handleClick,
    handleHover,
    smartFitAll,
    hoverInfo,
    isViewportReady,
  }
}
